/* Blocks */
.header {
  background-color: rgba(16, 16, 29, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  height: 100px;
  max-height: 100px;
  border: 1px solid #000;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}
.header__logo {
  font-family: "Lato Bold", sans-serif;
  font-size: 2.8rem;
  font-weight: 700;
}
@media screen and (max-width: 768px) {
  .header__logo {
    font-size: 2.4rem;
  }
}
@media screen and (max-width: 400px) {
  .header__logo {
    font-size: 2rem;
  }
}
.header__nav, .header__logo {
  text-transform: uppercase;
}
.header__nav ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 60px;
  font-family: "Lato Semibold", sans-serif;
  font-size: 1.3rem;
  font-weight: 600;
}
@media screen and (max-width: 768px) {
  .header__nav ul {
    gap: 20px;
    font-size: 1.2rem;
  }
}
.header__nav ul li {
  transition: color 0.3s ease-in-out;
}
@media (hover: hover) {
  .header__nav ul li:hover {
    color: #43ABF0;
  }
}
@media (hover: none) {
  .header__nav ul li:active {
    color: #43ABF0;
  }
}
@media screen and (max-width: 557px) {
  .header__nav {
    display: none;
  }
}
.header .burger-btn {
  display: none;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 557px) {
  .header .burger-btn {
    display: flex;
  }
}
.header .burger-btn__content {
  margin: 6px;
  padding: 0px;
  flex: 0 1 auto;
}
.header .burger-btn .ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.header .burger-btn .ham .top {
  stroke-dasharray: 40 139;
}
.header .burger-btn .ham .bottom {
  stroke-dasharray: 40 180;
}
.header .burger-btn .ham.active {
  transform: rotate(45deg);
}
.header .burger-btn .ham.active .top {
  stroke-dashoffset: -98px;
}
.header .burger-btn .ham.active .bottom {
  stroke-dashoffset: -138px;
}
.header .burger-btn .line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #fff;
  stroke-width: 5.5;
  stroke-linecap: round;
}

