.input {
  background-color: rgba(255, 255, 255, 0.85);
  outline: none;
  border: 1px solid #fff;
  border-radius: 3px;
  width: 100%;
  max-width: 370px;
  height: 48px;
  padding: 0 10px;
  font-size: 18px;
