.form {
  margin-top: 80px;
  display: grid;
  grid-template-columns: repeat(3, minmax(370px, 370px));
  grid-template-rows: repeat(2, 1fr);
  gap: 30px;
  grid-template-areas: "grid_a grid_b grid_c" "grid_d grid_d grid_e" ". grid_f .";
}
.form .grid-a {
  grid-area: grid_a;
}
.form .grid-b {
  grid-area: grid_b;
}
.form .grid-c {
  grid-area: grid_c;
}
.form .grid-d {
  grid-area: grid_d;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.form .grid-e {
  grid-area: grid_e;
}
.form .grid-f {
  grid-area: grid_f;
  padding-top: 10px;
}
@media screen and (max-width: 1260px) {
  .form {
    grid-template-columns: repeat(2, minmax(370px, 370px));
    grid-template-rows: repeat(3, 1fr);
    justify-content: center;
    gap: 20px;
    grid-template-areas: "grid_a grid_b" "grid_c grid_d" "grid_e grid_f";
  }
  .form .grid-f {
    padding-top: 0px;
  }
  .form__item-text {
    font-size: 1.4rem;
  }
}
